import request from '../../util/request.js';
export default {
	data() {
		return {
			pageName: 'search',
			searchValue: '',
			loading: false,
			init: false,
			resultData: null,
			list: []
		}
	},
	methods: {
		setValue(e) {
			this.searchValue = e.detail.value
		},
		handleSearch(page) {
			this.loading = true
			request.post('/search', {
				page,
				q: this.searchValue
			}).then(res => {
				console.log(res);
				if (page != 1) {
					this.list = this.list.concat(res.data)
				} else {
					this.list = res.data
				}
				this.resultData = res

				this.init = true
				this.loading = false
			})
		}
	},
	onReachBottom() {
		//上拉加载下一页
		if (this.resultData.IPageObj.current < this.resultData.IPageObj.pages) {
			this.handleSearch(this.resultData.IPageObj.current + 1)
		}
	}
}
